import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { Color } from 'three'
import { randInt } from 'three/src/math/MathUtils'
require('./KeyboardState')

// Keyboard controller
let keyboard = new KeyboardState();

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(90, sizes.width / sizes.height, 0.1, 1000)
camera.position.x = 5
camera.position.y = 5
camera.position.z = 5
camera.lookAt(new THREE.Vector3(0,0,0))
scene.add(camera)

const light = new THREE.AmbientLight('#ffffff', 1);
scene.add(light);   

const axesHelper = new THREE.AxesHelper(5);
// BLUE = Z
// RED = X
// GREEN = Y
//scene.add(axesHelper);

// Controls
const controls = new OrbitControls(camera, canvas)
controls.autoRotate = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
});
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 4))


const EMPTY = {type:'', color:0x030303, opacity:0};
var gameSize = 4;
var fields = [];
var zrow = [];
for(var i = 0; i < gameSize; i++) {
    zrow.push(EMPTY)
}
var yrow = [];
for(var i = 0; i < gameSize; i++) {
    yrow.push(structuredClone(zrow));
}
var field = [];
for(var i = 0; i < gameSize; i++) {
    field.push(structuredClone(yrow));
}


for(var x = 0; x < gameSize ; x++) {
    for(var y = 0; y < gameSize ; y++) {
        for(var z = 0; z < gameSize ; z++) {
        
            const geometry = new THREE.BoxGeometry( 1, 1, 1 );
            const material = new THREE.MeshBasicMaterial( {color: EMPTY.color, transparent:true, wireframe:false, opacity:EMPTY.opacity} );
            const cube = new THREE.Mesh( geometry, material );
            cube.position.setX(x-gameSize/2);
            cube.position.setY(y-gameSize/2);
            cube.position.setZ(z-gameSize/2);
            scene.add( cube );
            console.log(cube);
            
            field[x][y][z] = {
                three: cube,
                type: EMPTY.type
            };
        }
    }    
}

function setOnPoint(vector3d, a) {
    const x = vector3d[0];
    const y = vector3d[1];
    const z = vector3d[2];

    field[x][y][z].three.material.color.setHex(a.color);
    field[x][y][z].three.material.opacity = a.opacity;
    field[x][y][z].type = a.type;
    console.log(field);
    return;
}


const WALL = {type:'W', color:0x111111};
const HEAD = {type:'H', color:0x00ff00, opacity:1};
const BODY = {type:'B', color:0x00dd00, opacity:1};
const APPLE = {type:'A', color: 0xff0000, opacity:1};

var currApple = [gameSize-1,gameSize-1,gameSize-1];
var currHead = [0,0,0];
var currBody = [[0,0,1],[0,0,2]];
var currVertex = 8;
setOnPoint(currHead,HEAD);
setOnPoint(currApple, APPLE);
setOnPoint(currBody[0], BODY);
setOnPoint(currBody[1], BODY);

var directions = {
    'xplus': [1,0,0],
    'yplus': [0,1,0],
    'zplus': [0,0,1],
    'xminus': [-1,0,0],
    'yminus': [0,-1,0],
    'zminus': [0,0,-1]
}

function getOnPoint(vector3d, direction) {
    
    const x = vector3d[0] + directions[direction][0];
    const y = vector3d[1] + directions[direction][1];
    const z = vector3d[2] + directions[direction][2];

    if(x<0 || x >= gameSize || y<0 || y >= gameSize || z<0 || z >= gameSize) {
        return WALL;
    }
    console.log('get', x,y,z, field[x][y][z].type)
    return field[x][y][z].type;
}

function moveSnake(direction) {
    setOnPoint(currHead,EMPTY);
    setOnPoint(currBody[1],EMPTY);
    currBody[1] = currBody[0];
    currBody[0] = currHead;
    currHead = [
        currHead[0] + directions[direction][0],
        currHead[1] + directions[direction][1],
        currHead[2] + directions[direction][2],
    ];
    setOnPoint(currHead,HEAD);
    setOnPoint(currBody[0], BODY);
    setOnPoint(currBody[1], BODY);
}


function step() {
    // find next cell
    var dx = currApple[0] - currHead[0];
    var dy = currApple[1] - currHead[1];
    var dz = currApple[2] - currHead[2];

    
    if(dx > 0) {
        var stat = getOnPoint(currHead, 'xplus');
        if(stat == EMPTY.type) {
            moveSnake('xplus');
            return;
        }
        if(stat == APPLE.type) {
            moveSnake('xplus');
            makeNewApple();
            return;
        }
    }
    if(dx < 0) {
        var stat = getOnPoint(currHead, 'xminus');
        if(stat == EMPTY.type) {
            moveSnake('xminus');
            return;
        }
        if(stat == APPLE.type) {
            moveSnake('xminus');
            makeNewApple();
            return;
        }
    }
    if(dy > 0) {
        var stat = getOnPoint(currHead, 'yplus');
        if(stat == EMPTY.type) {
            moveSnake('yplus');
            return;
        }
        if(stat == APPLE.type) {
            moveSnake('yplus');
            makeNewApple();
            return;
        }
    }
    if(dy < 0) {
        var stat = getOnPoint(currHead, 'yminus');
        if(stat == EMPTY.type) {
            moveSnake('yminus');
            return;
        }
        if(stat == APPLE.type) {
            moveSnake('yminus');
            makeNewApple();
            return;
        }
    }
    if(dz > 0) {
        var stat = getOnPoint(currHead, 'zplus');
        if(stat == EMPTY.type) {
            moveSnake('zplus');
            return;
        }
        if(stat == APPLE.type) {
            moveSnake('zplus');
            makeNewApple();
            return;
        }
    }
    if(dz < 0) {
        var stat = getOnPoint(currHead, 'zminus');
        if(stat == EMPTY.type) {
            moveSnake('zminus');
            return;
        }
        if(stat == APPLE.type) {
            moveSnake('zminus');
            makeNewApple();
            return;
        }
    }
}

function makeNewApple() {
    var vertex = randInt(1,8);
    if(currVertex == vertex) {
        makeNewApple();
    }
    
    if(vertex == 1) { currApple = [0,0,gameSize-1] }
    if(vertex == 2) { currApple = [0,gameSize-1,0] }
    if(vertex == 3) { currApple = [gameSize-1,0,0] }
    if(vertex == 4) { currApple = [0,gameSize-1,gameSize-1] }
    if(vertex == 5) { currApple = [gameSize-1,0,gameSize-1] }
    if(vertex == 6) { currApple = [gameSize-1,gameSize-1,0] }
    if(vertex == 7) { currApple = [0,0,0] }
    if(vertex == 8) { currApple = [gameSize-1,gameSize-1,gameSize-1] }
    
    var simil = 0;
    if(currApple[0] == currHead[0]){
        simil++;
    } 
    if(currApple[1] == currHead[1]) {
        simil++;
    } 
    if(currApple[2] == currHead[2]) {
        simil++;
    }
    if(simil > 1) {
        console.log('simil', simil);
        makeNewApple();
    }
    
    
    currVertex = vertex;
    setOnPoint(currApple,APPLE);
}

var clock = new THREE.Clock();
var dt = 0;
const animate = () =>
{

    dt += clock.getDelta()

    requestAnimationFrame(animate)

    if (dt > 0.1)  {
        dt = 0;
        step()
    }

    // Update Orbital Controls
    controls.update()

    keyboard.update()

    // Render
    renderer.render(scene, camera)
}

animate()
